.h-sub-title {
    background: $gray;
    font-size: 18px;
    font-weight: 500;
    padding: 5px 25px;
    display: inline-block;
    border-radius: 20px;
    margin-bottom: 20px;
    font-family: "Barlow Condensed", sans-serif;
    color: $dark;
    i {
        color: $brand;
    }
}

.heading {
    font-weight: 700;
    font-size: 40px;
    text-transform: uppercase;
    font-family: "Barlow Condensed", sans-serif;
    margin-bottom: 20px;
    &-center {
        text-align: center;
    }
}

.sub-heading {
    font-size: 20px;
}

.main-page {
    margin-top: 90px;
}

.block-feedback {
    background: $dark;
    padding: 50px 0 20px;
    .heading {
        color: $light;
    }
    .sub-heading {
        max-width: 70%;
        text-align: center;
        margin: 0 auto;
        color: $light;
        margin-bottom: 40px;
    }
    .inner {
        background: $light;
        border-radius: 8px;
        padding: 20px;
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 30px;
        i {
            color: $brand;
            font-size: 22px;
        }
    }
}

.meta-people {
    display: flex;
    .avatar {
        width: 50px;
        height: 50px;
        border-radius: 50px;
        margin-right: 10px;
    }
    .name {
        font-weight: 700;
    }
    .job-title {
        font-size: 14px;
    }
}

.block-faq {
    margin: 50px 0;
    .sub-heading {
        margin-bottom: 30px;
    }
    .item-faq {
        padding: 20px;
        position: relative;
        &:not(:first-child) {
            border-top: 1px solid $gray;
        }
        &.active {
            background: $gray;
            border-radius: 8px;
            border-bottom: 0;
            .extend {
                &::after {
                    content: "\f056";
                }
            }
            .faq-name {
                margin-bottom: 15px;
            }
            &+.item-faq {
                border-top: 0;
            }
        }
    }
    .faq-name {
        font-size: 20px;
        font-weight: 700;
    }
    .extend {
        position: absolute;
        right: 20px;
        top: 20px;
        &::after {
            content: "\f055";
            font-family: Font Awesome\ 5 Pro;
            font-size: 20px;
            cursor: pointer;
        }
    }
    .faq-content {
        display: none;
    }
}

.block-expert {
    margin-top: 50px;
    .list-image-experts {
        .item {
            position: relative;
            &::before {
                content: "";
                width: 400px;
                height: 400px;
                background: url(../images/logo/logo-r.png) no-repeat;
                background-size: 100% 100%;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                z-index: -1;
                opacity: 0.05;
            }
            img {
                margin: 0 auto;
            }
        }
    }
    .apostrophe {
        font-size: 40px;
        color: $brand;
    }
    .meta-people {
        margin-top: 20px;
    }
    .slick-prev,
    .slick-next {
        transform: none;
    }
    .slick-prev {
        top: 100%;
        left: 80%;
    }
    .slick-next {
        top: 100%;
        right: 6%;
    }
}

.block-form {
    padding: 50px 0;
    background: $gray;
    .wrap-left {
        background: $dark;
        height: 100%;
        padding: 50px 50px 50px 70px;
        color: $light;
        border-bottom-right-radius: 60px;
        .logo {
            width: 100px;
            height: 114px;
            margin-bottom: 25px;
        }
        a {
            color: $light;
        }
    }
    .slogan {
        font-size: 24px;
        font-weight: 700;
        margin-bottom: 20px;
    }
    .sub-slogan {
        margin-bottom: 15px;
    }
    .info {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
    }
    .item {
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        i {
            width: 20px;
            margin-right: 5px;
        }
        span {
            font-weight: 700;
        }
    }
    .title-form {
        font-size: 24px;
        font-weight: 700;
        margin-bottom: 15px;
        text-transform: uppercase;
        font-family: "Barlow Condensed", sans-serif;
    }
    label {
        font-weight: 700;
        font-size: 15px;
    }
     ::placeholder {
        font-size: 15px;
    }
    .note {
        font-size: 14px;
        font-style: italic;
    }
}

.block-why-choose {
    margin-top: 50px;
    .heading {
        margin-bottom: 40px;
    }
    .list-content {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
        .item {
            position: relative;
            padding-left: 95px;
            &:not(:last-child) {
                margin-bottom: 15px;
            }
        }
        .icon {
            width: 50px;
            height: 50px;
            position: absolute;
            left: 30px;
            top: 20px;
        }
    }
    .title {
        font-size: 22px;
        font-weight: 600;
        font-family: Barlow Condensed, sans-serif;
    }
    .item {
        background: #fff2f2;
        padding: 15px 30px;
        border-radius: 8px;
        .text {
            margin-bottom: 0;
        }
    }
}

.block-banner {
    background: url(../images/banner-homepage.png) no-repeat top;
    background-size: cover;
    height: 90vh;
    position: relative;
    .banner-wrapper {
        height: 90vh;
    }
    .banner-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .heading-slider {
        font-size: 70px;
        font-family: "Barlow Condensed", sans-serif;
        font-weight: 700;
        margin-bottom: 35px;
    }
    .banner-logo {
        background: url(../images/home-banner.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: 70%;
        position: relative;
    }
    .banner-action {
        margin-bottom: 35px;
    }
    .quantity-user {
        font-size: 35px;
        font-weight: 700;
        font-family: "Barlow Condensed", sans-serif;
    }
    .total-user {
        position: relative;
    }
    .text-banner {
        font-size: 18px;
    }
    .icon {
        font-size: 20px;
        position: absolute;
        left: 0;
        top: 10px;
    }
    .total-mobile {
        padding-left: 30px;
    }
    .total-register {
        margin-left: 15px;
        .total-mobile {
            padding-left: 35px;
        }
    }
    .banner-logo-item {
        width: fit-content;
        &:first-child {
            left: 0;
            position: absolute;
            top: 18%;
        }
        &:nth-child(2n) {
            position: absolute;
            right: -5%;
            top: 45%;
        }
        &:last-child {
            bottom: 20%;
            left: 5%;
            position: absolute;
        }
        .item {
            background: $light;
            padding: 16px 20px 16px 16px;
            border-radius: 20px;
            align-items: center;
            box-shadow: 0 8px 24px 0 rgba(0, 48, 104, .04);
            display: inline-flex;
            transition: all .5s;
            font-family: "Barlow Condensed", sans-serif;
        }
        .web {
            font-size: 20px;
            font-weight: 500;
            line-height: 1.3;
        }
        .img-logo {
            width: 45px;
            margin-right: 10px;
        }
    }
}

.block-about {
    margin-top: 50px;
    .core-title {
        font-weight: 700;
        font-family: Barlow Condensed, sans-serif;
        text-transform: uppercase;
        font-size: 28px;
        color: $dark;
    }
    .core-item {
        align-items: center;
        padding: 20px 0;
        .text-content {
            background: $light;
            box-shadow: 0 3px 15px rgba(203, 139, 137, 0.1);
            padding: 30px 50px;
            border-radius: 8px;
            position: relative;
            z-index: 2;
        }
        .order-md-1.order-2 {
            .text-content {
                margin-right: -70px;
            }
        }
        .order-md-2.order-2 {
            .text-content {
                margin-left: -70px;
            }
        }
    }
}

.block-course {
    margin-top: 50px;
    margin-bottom: 120px;
    &-left {
        .course {
            .thumbnail {
                border-radius: 10px;
                margin-bottom: 15px;
            }
            &-title {
                font-size: 24px;
                font-weight: 600;
                font-family: Barlow Condensed, sans-serif;
                margin-bottom: 0;
            }
            &-meta {
                .meta {
                    list-style: none;
                    margin-bottom: 0;
                    padding-left: 0;
                    display: flex;
                    justify-content: space-between;
                }
                .item {
                    position: relative;
                    padding-left: 25px;
                    &::before {
                        font-family: Font Awesome\ 5 Pro;
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                    &:nth-child(1) {
                        &::before {
                            content: "\f5ff";
                            color: #6400ff;
                        }
                    }
                    &:nth-child(2) {
                        &::before {
                            content: "\f573";
                            color: #ff0000;
                        }
                    }
                    &:nth-child(3) {
                        &::before {
                            content: "\f017";
                            color: #fb6f30;
                        }
                    }
                }
                .text {
                    font-size: 14px;
                    font-weight: 600;
                }
            }
        }
    }
    .price-review {
        margin-top: 15px;
        margin-bottom: 15px;
        padding-bottom: 15px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #ededed;
    }
    .review {
        display: flex;
        align-items: center;
        &-star {
            list-style: none;
            padding-left: 0;
            margin-bottom: 0;
            margin-right: 15px;
            li {
                display: inline-block;
                color: #fb6f30;
            }
        }
        .number {
            font-size: 14px;
            font-weight: 500;
            color: #a5a5a5;
        }
    }
    .price {
        font-size: 22px;
        font-weight: 600;
        color: #d0021c;
        font-family: Barlow Condensed, sans-serif;
    }
    .heading {
        margin-bottom: 30px;
    }
    .course-info {
        i {
            color: $brand;
            width: 20px;
            margin-right: 5px;
            text-align: center;
        }
        span {
            font-size: 17px;
            font-weight: 600;
            font-family: "Barlow Condensed", sans-serif;
        }
        p {
            margin-bottom: 0px;
            padding: 8px 0;
            &:nth-child(2n + 1) {
                background: #00000003;
            }
        }
    }
    &-right {
        .title {
            font-size: 22px;
            font-weight: 700;
            font-family: "Barlow Condensed", sans-serif;
            text-transform: uppercase;
            margin-bottom: 15px;
        }
    }
    .tuition {
        &-title {
            margin-bottom: 10px;
        }
    }
    .content-title {
        font-weight: 600;
        font-family: "Barlow Condensed", sans-serif;
        font-size: 17px;
        margin-bottom: 10px;
        position: relative;
        &::after {
            content: "";
            width: 82px;
            height: 1px;
            background: $brand;
            position: absolute;
            left: 0;
            bottom: -3px;
        }
    }
    .content {
        p {
            padding: 0;
            margin-bottom: 10px;
        }
    }
    .slick-dots {
        bottom: -45px;
    }
}

.block-schedule {
    margin-bottom: 50px;
    .schedule-head {
        display: flex;
        border: 1px solid #9aa0a633;
        border-radius: 8px;
        margin-bottom: 10px;
        background: #9aa0a60d;
        .item {
            padding: 9px 12px;
            color: #80868b;
            display: flex;
            gap: 4px;
            align-items: center;
            &:not(:last-child) {
                border-right: 1px solid #9aa0a633;
            }
        }
    }
    .schedule-head,
    .schedule-container {
        display: flex;
        .item {
            &:nth-child(1) {
                flex: 1 1 0%;
            }
            &:nth-child(2) {
                width: 200px;
            }
            &:nth-child(3) {
                width: 140px;
            }
            &:nth-child(4) {
                width: 180px;
            }
            &:nth-child(5) {
                width: 240px;
            }
            &:nth-child(6) {
                width: 160px;
            }
        }
    }
    .schedule-container {
        padding-top: 12px;
        padding-bottom: 12px;
        &:nth-child(2n) {
            background: #9aa0a60d;
        }
        .item {
            padding-left: 10px;
            padding-right: 10px;
        }
    }
    .item-method {
        color: #ff8633;
        background: #ff771a1a;
        padding: 2px 8px 2px 25px;
        display: inline-block;
        border-radius: 20px;
        position: relative;
        &::before {
            content: "";
            background: #ff8633;
            width: 10px;
            height: 10px;
            border-radius: 10px;
            position: absolute;
            left: 8px;
            top: 10px;
        }
    }
}